import Vue from 'vue'
import invitePic from '../assets/image/invite-picture.png'
export default {
    data(){
        return{
            code:this.$route.query.code,
            invitePic: invitePic
        }
    },
    methods:{
        copyCode(){
            var code = this.$refs.code;
            code.select();
            console.log(code.value);
            if (document.selection) {
                var range = document.body.createTextRange();
                range.moveToElementText(this.$refs.codeInfo);
                range.select();
            } else if (window.getSelection) {
                var range = document.createRange();
                range.selectNode(this.$refs.codeInfo);
                window.getSelection().addRange(range);
            }
            try {
                if( document.execCommand("Copy")){
                    Vue.$vux.toast.show({text:"复制成功"});
                }else{
                    throw DOMException;
                }
            }catch (e){
                Vue.$vux.toast.show({text:"复制失败",type:"warn"});
            }


        }
    }
}



